<template>
<section>
        <button id = "info_button" class="button block" @click="isActive = !isActive">Info.</button>
        <b-notification class = "notify" :active.sync="isActive">
            DRAGANで64x64の画像を生成
            <br>
            64x64を256x256に拡大しているので画像が荒くなっています。
            <br>
            <a href = 'https://github.com/saitoujun/DRAGAN' target = "_blank">コードはGithubにあります。</a>
        </b-notification>
    </section>
</template>

<script>
export default {
  data() {
    return {
      isActive: false
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info_button {
  padding: 5px 30px;
  text-decoration: none;
  color: #ff8567;
  border: solid 2px #ff8567;
  border-radius: 3px;
  transition: 0.4s;
  margin: 0;
}
#info_button:hover {
  background: #ff8567;
  color: white;
}
.notify {
  width: 380px;
  height: 150px;
}
</style>



